<template>
    <div class="app-view app-generator">
        <generatorBefore v-model="before"></generatorBefore>
        <generatorAfter v-model="after"></generatorAfter>
    </div>
</template>

<script>
import generatorAfter from './after.vue'
import generatorBefore from './before.vue'


export default{
    components: {
        generatorAfter,
        generatorBefore
    },
    data() {
      return {
        names: {
            classname: "",
            animationname: "animation"
        },
        before:{
            css:{},
            transform:{},
            setting:{}
        },
        after:{
            css:{},
            transform:{},
            setting:{}
        }
      }
    },
    watch: {
        names: {
            handler:function(){
                this.setOutput(this.beforecss)
            },
            deep: true
        },
        before: {
            handler:function(){
                this.setOutput()
            },
            deep: true
        },
        after: {
            handler:function(){
                this.setOutput()
            },
            deep: true
        },
    },
    methods: {
      toggleButton: function (icon) {
        //   console.log(icon)
          if(icon.flag === true){
            icon.icon = "▶︎"
          }else{
            icon.icon = "▼"
          }
      },
      setOutput: function(){

          let outputcss;
          let classname = "." + this.names.classname;

          let transiton = "\ttransition: all ";
          if(this.after.setting.duration != undefined) transiton += this.after.setting.duration + "s ";
             else transiton += "1s";
          if(this.after.setting.delay != 0 && this.after.setting.delay != undefined) transiton += this.after.setting.delay + "s ";
          if(this.after.setting.timing != undefined)  transiton += this.after.setting.timing + " ";
          if(this.after.css.used){
            if(this.after.css.size.width.separate.used){ transiton += ",width " + this.after.css.size.width.separate.setting.duration+"s ";
                if(this.after.css.size.width.separate.setting.delay != 0) transiton += this.after.css.size.width.separate.setting.delay+"s ";
                if(this.after.css.size.width.separate.setting.timing != "") transiton += this.after.css.size.width.separate.setting.timing;
            }
          }

          transiton += ";"


        outputcss = classname + "-before{\n" + transiton + "\n";
          //before
          if(this.before.css.used){
              if(this.before.css.size.width.used) outputcss += "\twidth:" + this.before.css.size.width.num + this.before.css.size.width.unit + ";\n";
              if(this.before.css.size.height.used) outputcss += "\theight:" + this.before.css.size.height.num + this.before.css.size.height.unit + ";\n";
              if(this.before.css.bg.used) outputcss += "\tbackground-color:" + this.before.css.bg.color + ";\n";
              if(this.before.css.font.used) outputcss += "\tcolor:" + this.before.css.font.color + ";\n";
              if(this.before.css.font.used) outputcss += "\tfont-size:" + this.before.css.font.num + this.before.css.font.unit + ";\n";
              if(this.before.css.border.used) outputcss += "\tborder:" + this.before.css.border.thickness.num + this.before.css.border.thickness.unit + " " + this.before.css.border.color + " " + this.before.css.border.thickness.type + ";\n";
              if(this.before.css.radius.used) outputcss += "\tborder-radius:" + this.before.css.radius.num + this.before.css.radius.unit + ";\n";
              if(this.before.css.opacity.used) outputcss += "\topacity:" + this.before.css.opacity.num + ";\n";

          }
          if(this.before.transform.used && (this.before.transform.translate.used || this.before.transform.scale.used || this.before.transform.rotate.used || this.before.transform.skew.used)){
              outputcss += "\ttransform: ";
              if(this.before.transform.translate.used) outputcss += "translate(" + this.before.transform.translate.x.num + this.before.transform.translate.x.unit + "," + this.before.transform.translate.y.num + this.before.transform.translate.y.unit +") ";
              if(this.before.transform.scale.used) outputcss += "scale(" + this.before.transform.scale.num + ") ";
              if(this.before.transform.rotate.used) outputcss += "rotate(" + this.before.transform.rotate.num + "deg) ";
              if(this.before.transform.skew.used) outputcss += "skew(" + this.before.transform.skew.num + "deg) ";
              outputcss += ";\n"
              outputcss += "\t-webkit-transform: ";
              if(this.before.transform.translate.used) outputcss += "translate(" + this.before.transform.translate.x.num + this.before.transform.translate.x.unit + "," + this.before.transform.translate.y.num + this.before.transform.translate.y.unit +") ";
              if(this.before.transform.scale.used) outputcss += "scale(" + this.before.transform.scale.num + ") ";
              if(this.before.transform.rotate.used) outputcss += "rotate(" + this.before.transform.rotate.num + "deg) ";
              if(this.before.transform.skew.used) outputcss += "skew(" + this.before.transform.skew.num + "deg) ";
              outputcss += ";\n"
          }
          outputcss += "}\n";

          //after

          if(this.$parent.isLoop.flag){
            outputcss += classname + "{\n" + transiton + "\n";
          }else{
            outputcss += classname + ":hover{\n" + transiton + "\n";
          }
        //   console.log(this.after.css.used)
          if(this.after.css.used){
              if(this.after.css.size.width.used) outputcss += "\twidth:" + this.after.css.size.width.num + this.after.css.size.width.unit + ";\n";
              if(this.after.css.size.height.used) outputcss += "\theight:" + this.after.css.size.height.num + this.after.css.size.height.unit + ";\n";
              if(this.after.css.bg.used) outputcss += "\tbackground-color:" + this.after.css.bg.color + ";\n";
              if(this.after.css.font.used) outputcss += "\tcolor:" + this.after.css.font.color + ";\n";
              if(this.after.css.font.used) outputcss += "\tfont-size:" + this.after.css.font.num + this.after.css.font.unit + ";\n";
              if(this.after.css.border.used) outputcss += "\tborder:" + this.after.css.border.thickness.num + this.after.css.border.thickness.unit + " " + this.after.css.border.color + " " + this.after.css.border.thickness.type + ";\n";
              if(this.after.css.radius.used) outputcss += "\tborder-radius:" + this.after.css.radius.num + this.after.css.radius.unit + ";\n";
              if(this.after.css.opacity.used) outputcss += "\topacity:" + this.after.css.opacity.num + ";\n";

          }
          if(this.after.transform.used && (this.after.transform.translate.used || this.after.transform.scale.used || this.after.transform.rotate.used || this.after.transform.skew.used)){
              outputcss += "\ttransform: ";
              if(this.after.transform.translate.used) outputcss += "translate(" + this.after.transform.translate.x.num + this.after.transform.translate.x.unit + "," + this.after.transform.translate.y.num + this.after.transform.translate.y.unit +") ";
              if(this.after.transform.scale.used) outputcss += "scale(" + this.after.transform.scale.num + ") ";
              if(this.after.transform.rotate.used) outputcss += "rotate(" + this.after.transform.rotate.num + "deg) ";
              if(this.after.transform.skew.used) outputcss += "skew(" + this.after.transform.skew.num + "deg) ";
              outputcss += ";\n"
              outputcss += "\t-webkit-transform: ";
              if(this.after.transform.translate.used) outputcss += "translate(" + this.after.transform.translate.x.num + this.after.transform.translate.x.unit + "," + this.after.transform.translate.y.num + this.after.transform.translate.y.unit +") ";
              if(this.after.transform.scale.used) outputcss += "scale(" + this.after.transform.scale.num + ") ";
              if(this.after.transform.rotate.used) outputcss += "rotate(" + this.after.transform.rotate.num + "deg) ";
              if(this.after.transform.skew.used) outputcss += "skew(" + this.after.transform.skew.num + "deg) ";
              outputcss += ";\n"
          }
          outputcss += "}\n";

        //   outputcss =  outputcss + "\n" + hover + "{\n" + awidth + aheight + transform + transiton + "\n}";
          this.$parent.outputCSS(outputcss)
      }
    },
    computed: {
        
    },
  }

</script>

<style lang="scss">
    .app-generator{
        height: 100%;
        margin-top: 70px;
        overflow-y: scroll;
        // position: absolute;
        // right: 0;
    }
</style>