<template>
  <div id="app">
    <apphead></apphead>
    <div class="app-view app-result">
        <div class="result">
            <div class="result-view">
                <div class="x-line"></div>
                <div class="y-line"></div>
                <pre v-html="uerCSS"></pre>
                <div v-html="userHTML" ref="userorizin" style="position: absolute;top: 50%;left: 50%;-webkit-transform: translate(-50%, -50%);transform: translate(-50%, -50%);"></div>
            </div>
            <div class="result-buttons">
                <button class="result-loop" v-on:click="isLoop.flag=!isLoop.flag,toggleButton(isLoop)">{{this.isLoop.icon}}</button>
                <button class="result-menu" v-on:click="isMenu.flag=!isMenu.flag,toggleMenuButton(isMenu)">三</button>
                <div class="menu" v-bind:class="{active:isMenu.flag}">
                    ループ時間:<input type="number" name="" id="" v-model="time">ms
                </div>
            </div>
            <div class="result-edit">
                <p class="title">適用する要素</p>
                <textarea name="css" class="textarea" v-model="html" :rows="html.split(/\n/).length"></textarea>
            </div>
            <div class="result-edit">
                <p class="title">適用するCSS</p>
                <textarea name="css" class="textarea" v-model="css" :rows="css.split(/\n/).length"></textarea>
            </div>
            <div class="result-edit">
                <p class="title">出力</p>
                <div class="setname"><span>class</span><input type="text" name="" id="" v-model="classname"></div>
                <div class="setname"><span>animation name</span><input type="text" name="" id="" v-model="animename"></div>
                <textarea name="output" class="textarea" v-model="outputcss" :rows="outputcss.split(/\n/).length"></textarea>
                <pre v-html="setoutputcss"></pre>
            </div>
        </div>
    </div>
    <generator ref="generateReference"></generator>
  </div>
</template>

<script>
import apphead from '../components/head.vue'
import generator from './generator/generator.vue'

export default{
    components: {
        generator,
        apphead
    },
    data() {
      return {
          html:"<button class='button generate'>ボタン</button>",
          css:".button{background:red;width:100px;height:50px;}",
          uerCSS: "",
          userHTML:"",
          classname: "generate",
          animename: "animation",
          outputcss: "",
          setoutputcss: "",
          isLoop: {
              flag: false,
              icon : "▶︎"
          },
          isMenu: {
              flag: false,
              icon : "▶︎"
          },
          pid: null,
          time: 2000
      }
    },
    watch: {
      "html": function () {
        console.log('someObj changed')
        this.setUserOrizin()
      },
      "css": function () {
        console.log('someObj changed')
        this.setUserOrizin()
      },
      "classname": function(){
          console.log('someObj changed')
          this.setUserName()
          this.outputCSS(this.outputcss)
          this.$refs.userorizin.children[0].classList.add(this.classname+"-before") 
      },
      "isLoop.flag": function(){
          this.$refs.generateReference.setOutput()
          if(!this.isLoop.flag) this.$refs.userorizin.children[0].classList.add(this.classname) 
      },
      "time": function(){
          this.setLoop()
          if(!this.isLoop.flag) this.$refs.userorizin.children[0].classList.add(this.classname) 
      },
      "outputcss": function(){
          this.$refs.userorizin.children[0].classList.remove(this.classname+"-before") 
          this.$refs.userorizin.children[0].classList.add(this.classname+"-before") 
      }
    },
    methods: {
      setUserOrizin: function () { 
        return this.uerCSS = "<style>" + this.css + "</style>", this.userHTML = this.html;
      },
      setUserName: function(){
          this.$refs.generateReference.names.classname = this.classname;
          this.$refs.generateReference.names.animename = this.animename;
          
      },
      outputCSS: function(css){
        //   console.log("通過")
        //   console.log(css)
          this.setUserName()
          this.outputcss = css
          this.setoutputcss = "<style>" + css + "</style>";
        //   console.log(this.setoutputcss)
      },
      toggleButton: function (icon) {
          console.log(icon)
          if(icon.flag === false){
            icon.icon = "▶︎"
          }else{
            icon.icon = "ll"
          }
      },
      toggleMenuButton: function (icon) {
          console.log(icon)
          if(icon.flag === false){
            icon.icon = "三"
          }else{
            icon.icon = "×"
          }
      },
      setLoop(){
            let i = false;
            if(this.pid) clearInterval(this.pid)
            this.pid = setInterval(function () {
                if(this.isLoop.flag){
                console.log("Loop入りました")
                if(i) this.$refs.userorizin.children[0].classList.add('generate'), i = false
                    else this.$refs.userorizin.children[0].classList.remove('generate'), i = true
            }
            }.bind(this), this.time)
      }
    },
    created() {
        this.setUserOrizin()
        this.setLoop()
    },
}

</script>

