                border-bottom: 1px dashed #b6b6b6;
            }
        }
        .result-buttons{
            text-align: right;
            margin-top: 5px;
            .result-loop,.result-menu{
                -webkit-appearance: unset;
                width: 40px;
                height: 35px;
                background: #ffffff;
                outline: none;
                border-radius: 4px;
                font-size: 18px;
                color: #634c00;
                border-color: #e4c561;
            }
            .result-loop{
                margin-right: 5px;
            }
            .menu{
                display: none;
            }
            .active{
                display: block;
            }
        }
        .result-edit{
            border: 4px solid #e4c561;
            border-radius: 10px;
            margin: 15px 0;
            // background: #e4c561;
            color: #0c0c0c;
            .title{
                font-size: 23px;
                padding-left: 10px;
