<template>
    <table cellpadding="5" cellspacing="0" class="generator-setting">
        <tr class="generator-setting-head">
            <th class="generator-setting-head-text" v-on:click="isActiveSetting.flag=!isActiveSetting.flag,toggleButton(isActiveSetting)">setting</th><button class="button-dropdown" v-on:click="isActiveSetting.flag=!isActiveSetting.flag,toggleButton(isActiveSetting)">{{isActiveSetting.icon}}</button>
        </tr>
        <tr v-bind:class="{active:isActiveSetting.flag}" class="css">
            <td>
                <tr class="items">
                    <td class="item-title">duration</td>
                    <td class="item-setting">
                        <input type="number" max="1" min="0" v-model="setting.duration" class="item-input">
                        <input type="range" v-model="setting.duration" value="1" min="0.1" max="10" step="0.1" oninput="">
                    </td>
                </tr>
                <tr class="items">
                    <td class="item-title">delay</td>
                    <td class="item-setting">
                        <input type="number" max="1" min="0" v-model="setting.delay" class="item-input">
                        <input type="range" v-model="setting.delay" value="1" min="0.0" max="10" step="0.1" oninput="">
                    </td>
                </tr>
                <tr class="selects items">
                    <td class="item-title">timing-function</td>
                    <td class="item-setting">
                        <select multiple name=""  v-model="setting.timing" class="item-select">
                            <option selected value="">none</option>
                            <option>ease</option>
                            <option>ease-in</option>
                            <option>ease-out</option>
                        </select>
                    </td>
                </tr>
            </td>
        </tr>
    </table>
</template>

<script>

export default{
    components: {

    },
    props: {
    },
    data() {
      return {
        isActiveSetting: {
            flag: false,
            icon: "▶︎"
        },
        setting:{
            duration: 1,
            delay: 0,
            timing: []
        }
      }
    },
    watch: {
        setting: {
            handler:function(){
               this.$parent.setting = this.setting;
            },
            deep: true
        }
    },
    methods: {
        toggleButton: function(icon){
          console.log(icon)
          if(icon.flag === true){
            icon.icon = "▶︎"
          }else{
            icon.icon = "▼"
          }
        }
    },
    created() {
        this.$emit('input', this.setting)
    },
  }

</script>

